                fill-rule="evenodd"
              /></svg>
            </div>
          </button>
        </div>
        <button class="arrowButton leftButton" @click="previous()">
          <div>
            <svg
              fill="none"
              stroke="#bbb"
              width="22"
              height="22"
              viewBox="3 3 18 18"
              xmlns="http://www.w3.org/2000/svg"
