              stroke-linejoin="round"
            ><polyline points="15 18 9 12 15 6" /></svg>
          </div>
        </button>
        <button class="arrowButton rightButton"  @click="next()">
          <div>
            <svg
              fill="none"
              stroke="#bbb"
              width="22"
              height="22"
              viewBox="3 3 18 18"
              xmlns="http://www.w3.org/2000/svg"
